@import url('https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap');

:root {
  --primary-white: #ffffff;
  --primary-light-grey: #f4f4f4;
  --primary-grey: #ccd1d9;
  --primary-dark-grey: #666666;
  --primary-orange: #f9ab00;
  --primary-dark: rgb(52, 58, 64);
  --primary-red: #ef4035;
  --primary-hover-red: #f33501;
  --primary-black: #000000;
  --primary-green: green;
  --primary-transparent-black: rgba(0,0,0,0.7);

}

body {
  font-family:  "Roboto Condensed", sans-serif !important;
}

/******************************* NAVBAR **********************/

nav {
  z-index: 2;
}

.logo {
  width: 6rem;
}

/*------------------ MENU LINKS -------------*/
nav a.nav-link {
  font-size : .9rem;
  letter-spacing: 0.0375rem;
  cursor: pointer;

}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: var(--primary-white) !important;
  /* border-top: 0.1875rem solid var(--primary-dark); */
}

.navbar-brand {
  max-width : 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
}

@media(max-width: 768px) {
  .navbar-brand {
    width: 6rem;
  }

}

.navbar-nav > li > a.active, .navbar-nav > li > a:focus {
  border-top: 0.1875rem solid var(--primary-orange);
}

.navbar-nav > li > a:hover {
  border-top: 0.1875rem solid var(--primary-orange);
}


/******************************* HEADER **********************/

.header-wrapper {
  position: relative;
  background: url(background-2.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 100vh;
}

.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  font-family:  "Roboto Condensed", sans-serif;
  z-index: 1;
}

.main-info h1 {
  color: var(--primary-orange);
  text-transform: uppercase;
}

/*----------------- TYPED TEXT ------------------*/
.typed-text {
  font-size: 2rem;
  color: var(--primary-white);
}

@media(max-width: 768px) {
  .main-info h1 {
    font-size: 1.7rem !important;
  }
  .typed-text {
    font-size: 1.3rem !important;
  }

}

/*----------------- HEADER MAIN OFFER BUTTON ------------------*/
.btn-main-offer {
  border: 1px solid var(--primary-red);
  text-transform: uppercase;
  border-radius: 0;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: var(--primary-white);
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: var(--primary-hover-red);
  color: var(--primary-white);
  transition: .2s ease-in-out;
}

/*----------------- PARTICLES JS ------------------*/
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vw;
  z-index: 1;
}

@media(max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

/******************************* ABOUT ME **********************/
.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-img {
  width: 18rem;
  border-radius: 50%;
  padding: .3rem;
  border: 1px solid var(--primary-orange);
}

.about-heading {
  color: var(--primary-orange);
  text-transform: uppercase;
}

.about-heading {
  margin-left: 5rem;
}

.about-p {
  margin: 4rem;
  font-size: 1.3rem;
  border-top: 0.1875rem solid var(--primary-dark);
}

@media(max-width: 468px) {
  .profile-img {
    width: 10rem;
  }
  .about-heading {
    margin-left: 7rem;
  }
  .about-p {
    margin: 2rem;
    font-size: 0.95rem;
    border-top: 0.1875rem solid var(--primary-dark);
  }
}

/******************************* SERVICES **********************/

.services {
  text-align: center;
  padding-bottom: 2rem;
  background: var(--primary-light-grey);
}

.services h1{
  color: var(--primary-dark);
  text-transform: uppercase;
}

.services .circle {
  position: relative;
  margin: 0.375rem auto;
  background: var(--primary-orange);
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.services .icon {
  font-size: 1.4rem;
  color: var(--primary-white);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.services .box {
  height: 16rem;
  border-bottom: 0.3125rem solid var(--primary-orange);
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: var(--primary-white);
  transition: 0.3s ease-in-out;
}

.services .box:hover {
  background: var(--primary-orange);
  border-bottom: 0.3125rem solid var(--primary-dark);
}

.services .box:hover .icon {
  color: var(--primary-dark);
}

.services .box:hover .circle {
  background: var(--primary-white);
}

/******************************* EXPERIENCE **********************/

.experience {
  padding: 0.7rem;
}

.experience-wrapper {
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}

.experience h1 {
  text-transform: uppercase;
  color: var(--primary-orange);
}

.experience-wrapper:before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.125rem;
  height: 100%;
  background: var(--primary-grey);
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
  clear: both;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid var(--primary-dark);
  background: var(--primary-orange);
  margin-top: 0.625rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: var(--primary-dark-grey);
  /* direction: ltr; */
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: var(--primary-hover-red);
  font-weight: 900;
}

/*------------------- MEDIA ------------------------*/
@media(max-width: 768px) {
  .experience .container {
    padding-left: 0;
  }

  .experience-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    float: none;
    width: 100%;
  }

  .timeline-block-left {
    float: none;
    width: 100%;
    direction: ltr;

  }

  .timeline-content {
    margin: 1.5rem 0;
  }
  
}

/******************************* React **********************/

.portfolio-wrapper {
  background: var(--primary-light-grey);
  padding: 3rem 0;
}

.portfolio-wrapper h1 {
  color: var(--primary-orange);
} 

.portfolio-wrapper h4 {
  color: var(--primary-black);
  background: var(--primary-orange);
  display: inline-block;
  border: 1px solid var(--primary-orange);
  padding: 1rem 2rem 1rem 2rem;
  
} 

.portfolio-image {
  width: 15rem;
  border: 1px solid var(--primary-orange);
  padding: 0 0.5rem;
}

.portfolio-image-box {
  position: relative;
  margin: 1rem;
}

@media(max-width: 768px) {
  
  .portfolio-wrapper h4 {
    display: block;
    width: 70%;
    padding: 1rem 0.5rem 0.5rem 0.8rem;  
  } 
  
}

/*------------------- OVERFLOW BOX ------------------------*/

.overflow {
  position: absolute;
  opacity: 0;
  background: var(--primary-black);
  width: 15rem;
  height: 9rem;
  top: 0;
  border: 1px solid var(--primary-red);
  cursor: pointer;

}

.portfolio-image-box:hover .overflow {
  opacity: 60%;
  transition: 0.3s ease-in-out;
}

.portfolio-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.6rem;
  color: var(--primary-orange);
  font-size: 1.5rem;
}

.portfolio-image-box:hover .portfolio-icon {
  opacity: 70%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

.popupbox-wrapper {
  margin-top: 7rem;
}

.popupbox-content h1 {
  padding: 1.5rem 0 1.5rem 0;
  margin-left: 0.75rem;
}

/*------------------- OVERFLOW BOX ------------------------*/

.portfolio-image-popupbox {
  width: 45rem;
  padding: 0 0.5rem;
}

.hyper-link {
  cursor: pointer;
  color: var(--primary-orange);

}

.hyper-link:hover {
  color: var(--primary-red);
  transition: 0.3s ease-in-out;
}

/*------------------- MEDIA  ------------------------*/
@media(max-width: 768px) {
  .portfolio-image-popupbox {
    width: 100%;
  }
}

@media(max-height: 570px) {

  .popupbox-wrapper {
    height: 100%
  }
  .portfolio-image-popupbox {
    width: 50%;
  }
}

@media(max-height: 280px) {

  .popupbox-wrapper {
    height: 90%
  }
  .portfolio-image-popupbox {
    width: 20%;
  }
}

/******************************* Hobbies **********************/

.testimonials {
  background-image: url(bg-image.png);
  background-size: cover;
  background-color: var(--primary-black);
  text-align: center;
  padding-bottom: 4rem;
  margin-bottom: 0;
  width: 100%;
  
}

.testimonials h1 {
  color: var(--primary-orange);
  font-weight: 600;
  padding: 3rem 0;
  text-transform: uppercase;
}

.testimonials-content {
  text-align: center;
  max-width: 53.125rem;
  margin: 0 auto 2rem auto;
  background: var(--primary-transparent-black);
}

.myCarousel {
  color: var(--primary-white);
  background: transparent;
  margin-top: -6%;
  width: 54%;
  margin-left: auto;
  margin-right: auto;
  padding-top: 6%;
  padding-bottom: 8%;
  padding-right: 5%;
  padding-left: 5%;
  height: 17.875rem;
}

.myCarousel h3 {
  color: var(--primary-orange);
  margin-top: 6%;
  margin-bottom: 8%;
  font-weight: 600;
  text-transform: uppercase;
}

.myCarousel p {
  color: var(--primary-grey);
}

.carousel .slide img {
  width: 35rem !important;
  border-radius: 5%;
}

.carousel .slide {
  background: transparent !important;
  height: 100% !important;
  padding-top: 2rem;
}

.carousel-root {
  margin: auto !important;
  margin-top: 3% !important;

}

/*------------------- MEDIA  ------------------------*/
@media(max-width: 768px) {
  .carousel .slide img {
    width: 65% !important;
  }

  .myCarousel {
    height: 22rem;
  }

  .myCarousel h3 {
    margin-top: 6%;
    margin-bottom: 8%;
    font-weight: 200;
  }

  .myCarousel p {
    margin-top: 6%;
    margin-bottom: 8%;
    padding-top: 6%;
    padding-bottom: 8%;
  }
  
}

@media(max-height: 570px) {
  .myCarousel h3 {
    margin-top: 6%;
    margin-bottom: 8%;
    font-weight: 200;
  }
  
}


/******************************* CONTACTS COMPONENT**********************/

.contacts {
  background: var(--primary-dark);
  padding: 3rem 0;
}

.contacts h1 {
  color: var(--primary-orange);
  padding: 1.3rem 0;
  text-transform:  uppercase;
}

.contacts p {
  color: var(--primary-light-grey);
  font-size: 1.2rem;
}

/*------------------- INPUTS  ------------------------*/

.contacts input, .contacts textarea {
  width: 100%;
  box-sizing: border-box;
  background: none;
  resize: none;
  border: 0;
  border-bottom: 0.125rem solid var(--primary-dark-grey);
  border-radius: 0;
  color: var(--primary-orange);
  outline: 0 !important;
  box-shadow: none !important;
  top: 0;
  margin-bottom: 1rem;
  padding-left:  0 !important;
}

.contacts .container {
  padding: 4rem 0;

}

.contacts input:focus, .contacts textarea:focus {
  background: transparent;
  color: var(--primary-orange);
  border-bottom:  0.125rem solid var(--primary-dark-grey);
}

.contacts textarea {
  height: 12.2rem !important;

}

.contact-btn {
  margin: 1rem 0 0 0;
  width: 100%;
  background: none;
}

.line {
  position: relative;
  top: -0.8rem;
  width: 0%;
  height: 0.125rem;
  background: var(--primary-orange);
  display: inline-block;

}

input:focus+.line, textarea:focus+.line {
  width: 100%;
  transition: width 0.5s ease-in-out;
}

input.form-control {
  margin: -0.3rem 0;
}

textarea.form-control {
  margin: -0.3rem 0;
}

.error-message {
  padding-top: 0.45rem;
  color: var(--primary-red);
  text-align: left;
}

.successMessage {
  font-weight: 900;
  color: var(--primary-green);
}

/*------------------- MEDIA  ------------------------*/

@media(max-width: 768px) {
  .contacts {
    overflow-x: hidden;
  }

  
  .contacts .container {
    padding: 0 1rem;
    margin: 4rem 0 2rem 0;
  }

  .contacts p {
    padding: 0 0.4rem;
  }
}



/******************************* FOOTER COMPONENT**********************/

.footer {
  background: var(--primary-black);
  color: var(--primary-dark-grey);
  padding: 3rem;
}

.footer a {
  text-decoration: none;
  color: var(--primary-dark-grey);
}

.footer a:hover {
  text-decoration: none;
  color: var(--primary-orange);
  cursor:pointer;
}

.footer p {
  margin-bottom: 0;
}

/*------------------- MEDIA  ------------------------*/

@media(max-width: 768px) {
  .d-flex a {
    margin: 1rem 0 1rem 0;
  }
  .col-lg-4 {
    margin: 1.5rem 0 1.5rem 0;
  }
  .col-lg-3 {
    margin: 1.5rem 0 1.5rem 0;
  }
 .pt-3 {
   margin-top: 1.5rem;
 }
}